'use client'

import { useState, useMemo } from 'react'
import Link from 'next/link'
import Image from 'next/image'
import { properties, Property } from '@/lib/properties'

export default function ListingsPage() {
  const [filters, setFilters] = useState({
    type: 'all',
    priceRange: 'all',
    bedrooms: 'all',
    location: ''
  })

  const filteredProperties = useMemo(() => {
    return properties.filter(property => {
      // Filter by type
      if (filters.type !== 'all' && property.type !== filters.type) {
        return false
      }

      // Filter by location (simple text search)
      if (filters.location && !property.address.toLowerCase().includes(filters.location.toLowerCase())) {
        return false
      }

      // Filter by bedrooms
      if (filters.bedrooms !== 'all') {
        const bedroomCount = parseInt(filters.bedrooms)
        if (property.features.bedrooms !== bedroomCount) {
          return false
        }
      }

      // Filter by price range (simplified for demo)
      if (filters.priceRange !== 'all') {
        const price = property.price.replace(/[^0-9]/g, '')
        const priceNum = parseInt(price)
        
        switch (filters.priceRange) {
          case 'under-500k':
            return priceNum < 500000
          case '500k-800k':
            return priceNum >= 500000 && priceNum <= 800000
          case 'over-800k':
            return priceNum > 800000
          case 'under-2k':
            return property.type === 'rent' && priceNum < 2000
          case '2k-3k':
            return property.type === 'rent' && priceNum >= 2000 && priceNum <= 3000
          case 'over-3k':
            return property.type === 'rent' && priceNum > 3000
          default:
            return true
        }
      }

      return true
    })
  }, [filters])

  const handleFilterChange = (key: string, value: string) => {
    setFilters(prev => ({ ...prev, [key]: value }))
  }

  return (
    <div className="container mx-auto px-4 py-8">
      {/* Header */}
      <div className="mb-8">
        <h1 className="text-3xl font-bold mb-4">Property Listings</h1>
        <p className="text-gray-600">
          Showing {filteredProperties.length} of {properties.length} properties
        </p>
      </div>

      {/* Filters */}
      <div className="bg-white p-6 rounded-lg shadow-md mb-8">
        <h2 className="text-lg font-semibold mb-4">Filter Properties</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          {/* Property Type */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Property Type
            </label>
            <select
              value={filters.type}
              onChange={(e) => handleFilterChange('type', e.target.value)}
              className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            >
              <option value="all">All Types</option>
              <option value="sale">For Sale</option>
              <option value="rent">For Rent</option>
            </select>
          </div>

          {/* Location */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Location
            </label>
            <input
              type="text"
              placeholder="Enter city or area"
              value={filters.location}
              onChange={(e) => handleFilterChange('location', e.target.value)}
              className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            />
          </div>

          {/* Bedrooms */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Bedrooms
            </label>
            <select
              value={filters.bedrooms}
              onChange={(e) => handleFilterChange('bedrooms', e.target.value)}
              className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            >
              <option value="all">Any</option>
              <option value="0">Studio</option>
              <option value="1">1 Bedroom</option>
              <option value="2">2 Bedrooms</option>
              <option value="3">3 Bedrooms</option>
              <option value="4">4+ Bedrooms</option>
            </select>
          </div>

          {/* Price Range */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Price Range
            </label>
            <select
              value={filters.priceRange}
              onChange={(e) => handleFilterChange('priceRange', e.target.value)}
              className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            >
              <option value="all">Any Price</option>
              {filters.type === 'rent' || filters.type === 'all' ? (
                <>
                  <option value="under-2k">Under $2,000/month</option>
                  <option value="2k-3k">$2,000 - $3,000/month</option>
                  <option value="over-3k">Over $3,000/month</option>
                </>
              ) : null}
              {filters.type === 'sale' || filters.type === 'all' ? (
                <>
                  <option value="under-500k">Under $500k</option>
                  <option value="500k-800k">$500k - $800k</option>
                  <option value="over-800k">Over $800k</option>
                </>
              ) : null}
            </select>
          </div>
        </div>

        {/* Clear Filters */}
        <div className="mt-4">
          <button
            onClick={() => setFilters({ type: 'all', priceRange: 'all', bedrooms: 'all', location: '' })}
            className="text-blue-600 hover:text-blue-800 font-medium"
          >
            Clear All Filters
          </button>
        </div>
      </div>

      {/* Properties Grid */}
      {filteredProperties.length > 0 ? (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {filteredProperties.map((property) => (
            <Link
              key={property.id}
              href={`/property/${property.id}`}
              className="group bg-white rounded-lg shadow-lg overflow-hidden hover:shadow-xl transition-shadow duration-300"
            >
              <div className="relative h-64 overflow-hidden">
                <Image
                  src={property.imageUrl}
                  alt={property.title}
                  fill
                  className="object-cover group-hover:scale-105 transition-transform duration-300"
                />
                <div className="absolute top-4 left-4">
                  <span className={`px-3 py-1 rounded-full text-sm font-semibold ${
                    property.type === 'sale' 
                      ? 'bg-green-100 text-green-800' 
                      : 'bg-blue-100 text-blue-800'
                  }`}>
                    For {property.type === 'sale' ? 'Sale' : 'Rent'}
                  </span>
                </div>
              </div>
              
              <div className="p-6">
                <h3 className="text-xl font-semibold mb-2 group-hover:text-blue-600 transition-colors">
                  {property.title}
                </h3>
                <p className="text-gray-600 mb-3 text-sm">{property.address}</p>
                <div className="flex justify-between items-center mb-4">
                  <span className="text-2xl font-bold text-blue-600">{property.price}</span>
                </div>
                <div className="flex justify-between text-sm text-gray-500 mb-4">
                  <span>{property.features.bedrooms === 0 ? 'Studio' : `${property.features.bedrooms} bed`}</span>
                  <span>{property.features.bathrooms} bath</span>
                  <span>{property.features.area}</span>
                </div>
                <p className="text-gray-600 text-sm line-clamp-2">
                  {property.description}
                </p>
              </div>
            </Link>
          ))}
        </div>
      ) : (
        <div className="text-center py-12">
          <div className="text-6xl mb-4">🏠</div>
          <h3 className="text-xl font-semibold mb-2">No Properties Found</h3>
          <p className="text-gray-600 mb-4">
            Try adjusting your filters to see more results.
          </p>
          <button
            onClick={() => setFilters({ type: 'all', priceRange: 'all', bedrooms: 'all', location: '' })}
            className="bg-blue-600 text-white px-6 py-2 rounded-lg hover:bg-blue-700 transition-colors"
          >
            Clear All Filters
          </button>
        </div>
      )}

      {/* Load More (placeholder for future pagination) */}
      {filteredProperties.length > 0 && (
        <div className="text-center mt-12">
          <button className="bg-gray-200 text-gray-700 px-8 py-3 rounded-lg font-semibold hover:bg-gray-300 transition-colors">
            Load More Properties
          </button>
        </div>
      )}
    </div>
  )
}
